/*Reset*/
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
/*Utilities*/
p{
    color: darkgray;
}
h2{
    color: #fff;
    font-size: 4rem;
}
/*navbar*/
.logo{
    text-align: center;
    align-items: center;
    background-color: #fff;
    color: red;
    margin: 50px auto 0px auto;
    font-size: 50px;
    font-weight: bolder;
    border-radius: 50%;
    padding-top: 25px;
    width: 100px;
    height: 100px;
    
}
/*Quick Searches*/
.row{
align-items: center;
justify-content: center;

}
.l-heading{
    color:#192f60;
    font-size: 30px;
    font-weight: bold;
}
.box-content .l-heading{
    font-size: 25px;
    font-weight: bolder;
}
.message{
    font-size: 18px;
    padding: 5px 0 10px 0;
}
.main-section{
    display: flex;
    height: 400px;
    text-align: center;
    align-items: center;
   
    flex-direction: column;
    background: url('https://images.unsplash.com/photo-1447078806655-40579c2520d6?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80') no-repeat center center/cover;
}
.form-group .form-control{
width: 100%;
height: 50px;
}
.input-group input.form-control{
    width: 100%;
    height: 50px;
    }

    .col-lg-4 {
        background-color: #fff;
        color: #192f60;
        box-shadow: 0 3px 6px #000;
       margin-right: 25px;
        width: 350px;
    }
    .box-img{
        width: 50%;
        float: left;
        padding-left: 0px;
        padding-right: 0px;
    }
    .Block-img{
        width: 150px;
        height: 160px;
    }
    @media screen and (max-width:720px) {
        .container {
            margin-left: 80px;
        }
        .box{
            margin-bottom: 50px;
        }
    }
  